.paralax-background{
  background-color: blue;
  width: 100%;
  height: 550px;
  position: fixed;
  top: 0px;
  z-index: -1;
}

.homepage .content-container{
  padding-top: 550px;
}
.homepage .content{
  position: relative;
  padding-top: 5px;
  padding-right: 15px;
}

.prof-pic-container{
  width: 200px;
  height:200px;
  position: absolute;
  top:-100px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 9px;
  color:black !important;
}
#name{
  margin: 0px;
}
.profile{
  position: absolute;
  top:0px;
  width: 250px;
  padding-top: 100px;
  display: flex;
  align-items: center;
  flex-flow: column;
  height: calc(100% - 100px);
  background-color: #0842cc;
}
.profile > * {
  color: white;
}
.prof-pic{
  width: 100%;
  height: auto;
}
.sidebar{
  display: flex;
  height: 100%;
  align-items: flex-start;
  flex-flow: column;
  padding-left: 15px;
}
.sticky-sidebar{
  position: sticky; 
  top: 0px;
}
.homepage .content{
  background-color: white;
}

.main{
  margin-left: 250px;
  padding-left: 15px;
}

/* RESUME */

.resume-header{
  text-align: center;
  background-color: lightgrey;
}

/*Carousel*/

.carousel {
  position: relative;
  width: 500px;
  margin: 0 auto;
  height: auto;
  overflow: hidden;
  white-space: nowrap;
}

.slide-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}
.slide img{
  max-width: 100%;
}

.arrow{
  height: 50px;
  width: 50px;
  position: absolute;
  top: 50%;
  border-radius: 25px;
  background-color: rgba(255, 255, 255, 0.4);
}
.arrow img {
  width: 100%;
  height: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  transform: translateX(9px);
}

.prev-arrow{
  transform: scaleX(-1);
  left:0px; 
}
.next-arrow{
  right: 0px;
}